.appreciation {
  position: relative;
  padding: 63px 20px;
  padding-top: 40px;
  color: #ffffff;
  background-color: #2295ff;
}

.appreciation__title {
  margin-bottom: 4px;
}

.appreciation__desc {
  margin-bottom: 60px;
  font-size: 18px;
  line-height: 190%;
  font-family: $second-font;
  text-transform: uppercase;
}


.appreciation__box {
  position: relative;
  margin-bottom: 134px;
  padding: 60px 20px;
  padding-top: 42px;
  padding-left: 23px;
  background-color: #0e5b74;
}

.appreciation__name {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 30px;
  line-height: 35px;
  font-family: $second-font;
  text-transform: uppercase;

  & b {
    font-weight: 900;
  }

  & span {
    font-size: 30px;
    line-height: 40px;
    font-family: $first-font;
  }
}

.appreciation__text {
  font-size: 20px;
  line-height: 170%;
}

.appreciation__link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: -168px;
  padding: 51px 33px 31px 34px;
  background-image: url("../img/link-background.svg");
  background-repeat: no-repeat;
  background-size: cover;


  & p {
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
  }
}

.appreciation__icon {
  width: 22px;
  height: 22px;
  margin-right: 14px;
  fill: #ffffff;
}

.appreciation__control {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.appreciation__image {
  display: none;
}

.appreciation__wrap {
  margin-bottom: 60px;
}

@media (min-width: 1200px) {
  .appreciation {
    display: flex;
    padding-top: 56px;
    padding-right: calc(50% - 555px);
    padding-bottom: 114px;
    padding-left: calc(50% - 638px);
  }


  .appreciation__list {
    width: 1190%;
    max-width: 1190px;
  }

  .appreciation__item {
    padding: 0 5px;
  }

  .appreciation__title {
    margin-left: 75px;
  }

  .appreciation__container {
    position: relative;
  }

  .appreciation__desc {
    margin-bottom: 85px;
    margin-left: 75px;
    font-size: 24px;
    line-height: 190%;
    text-align: left;
  }

  .appreciation__box {
    margin-bottom: 60px;
    padding: 66px 98px 74px 72px;

    &::before {
      @include pseudo;
      position: absolute;
      top: -45px;
      left: 0;
      width: 100%;
      height: 46px;
      background-image: url("../../img/appreciation__box-top.svg");
      background-position: top;
      background-size: cover;
    }

    &::after {
      @include pseudo;
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 100%;
      height: 21px;
      background-image: url("../../img/appreciation__box-bottom.svg");
      background-position: top;
      background-size: cover;
    }
  }

  .appreciation__text br {
    display: none;
  }

  .appreciation__link {
    position: relative;
    bottom: 0;
    z-index: 1000;
    justify-content: center;
    margin-top: -36px;
    padding-bottom: 34px;
  }

  .appreciation__wrapper {
    order: -1;
  }

  .appreciation__control {
    position: absolute;
    right: 182px;
    bottom: 40px;
    max-width: 365px;
    margin: 0 auto;
  }

  .appreciation__image {
    display: block;
    padding: 66px 64px 31px 45px;
    background-color: #ffffff;
  }

  .appreciation__wrap {
    display: flex;
  }

  .appreciation__img {
    width: 353px;
    min-width: 353px;
  }
}
